import { createElement } from '../../render.js';

function createEventDetailsTemlate() {
  return `<section class="event__details">
  </section>`;
}

export default class EventDetailsView {

  getTemplate() {
    return createEventDetailsTemlate();
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }
    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
